.nav-bar{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: solid 3px rgba(0,0,0,.2);
    background: rgb(190,0,0);
    color: #ffffff;
}

.logo{
    -ms-flex: 2 1;
        flex: 2 1;
    font-size: 2.2em;
    padding-left: 15px;
}

.nav-links{
    -ms-flex: 8 1;
        flex: 8 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    font-size: 1.5em;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

a{
    text-decoration: none;
    color: inherit;
}

.nav-links a{
    /*border-left: solid rgba(0,0,0,.05);*/
    padding: 20px 15px;
}

.nav-links a:hover{
    background: rgb(175,0,0);
}

.nav-links a:active{
    background: rgb(160,0,0);
}


